import auth0 from 'auth0-js';
import history from '../history';

export default class Auth {
  auth0 = new auth0.WebAuth({
    domain: 'bhgoal.auth0.com',
    clientID: 'pzGMlOALqUYtYbLeHkMRRmpMED8uqmRY',
    redirectUri: (((process.env.NODE_ENV === 'production') ? "https://coffee-shop-sim.herokuapp.com" : "http://localhost:3000") + "/callback"),
    audience: 'https://bhgoal.auth0.com/userinfo',
    responseType: 'token id_token',
    scope: 'openid profile'
  });

  constructor() {
    this.login = this.login.bind(this);
    this.logout = this.logout.bind(this);
    this.handleAuthentication = this.handleAuthentication.bind(this);
    this.isAuthenticated = this.isAuthenticated.bind(this);
  }

  handleAuthentication() {
    this.auth0.parseHash((err, authResult) => {
      if (authResult && authResult.accessToken && authResult.idToken) {
        this.setSession(authResult);
        history.replace('/');
      } else if (err) {
        history.replace('/');
        console.log(err);
      }
    });
  }

  setSession(authResult) {
    // Set the time that the Access Token will expire at
    let expiresAt = JSON.stringify((authResult.expiresIn * 1000) + new Date().getTime());
    localStorage.setItem('access_token', authResult.accessToken);
    localStorage.setItem('id_token', authResult.idToken);
    localStorage.setItem('expires_at', expiresAt);
    // navigate to the home route
    history.replace('/');
  }

  login() {
    this.auth0.authorize();
  }

  logout() {
    // Clear Access Token and ID Token from local storage
    localStorage.removeItem('access_token');
    localStorage.removeItem('id_token');
    localStorage.removeItem('expires_at');
    // navigate to the home route
    history.replace('/');
  }

  isAuthenticated() {
    // Check whether the current time is past the 
    // Access Token's expiry time
    let expiresAt = JSON.parse(localStorage.getItem('expires_at'));
    return new Date().getTime() < expiresAt;
  }

  

  getProfile() {

    var accessToken = localStorage.getItem('access_token');

    if (!accessToken) {
      console.log('Access Token must exist to fetch profile');
    }
    let userProfile = this.auth0.client.userInfo(accessToken, function(err, profile) {
      if (profile) {
        console.log(profile);
        userProfile = profile;
        console.log(userProfile);
        displayProfile(userProfile);
        return profile;
      }
    });

    
    function displayProfile(profile) {
      userProfile = profile;
      console.log(userProfile);
      this.profileResponse(userProfile);
    }
    console.log(userProfile);
    return userProfile;
  }

  profileResponse(profile) {
    console.log(profile);
  }


}


